/**
 * Oblicza kt lotu rakiety wzgldem powierzchni Ziemi (gravity turn) na podstawie wysokoci.
 * 
 * @param {number} height - Wysoko rakiety nad powierzchni Ziemi w metrach.
 * @param {number} maxAngle - Maksymalny kt (na przykad 90 stopni) w stopniach.
 * @param {number} turnStartHeight - Wysoko, na ktrej zaczyna si gravity turn w metrach.
 * @param {number} turnEndHeight - Wysoko, na ktrej gravity turn jest zakoczony w metrach.
 * @returns {number} - Kt lotu rakiety w stopniach.
 */
function gravityTurnAngle(height : number, maxAngle = 85, turnStartHeight = 100, turnEndHeight = 110000) {
    // Upewnij si, e wysoko jest w granicach od startu do koca gravity turn
    if (height < turnStartHeight) {
        //console.log("Rakieta jeszcze si nie obrcia")
        return 0; // Rakieta jeszcze si nie obrcia
        
    }
    if (height > turnEndHeight) {
        //console.log(height)
        return maxAngle; // Rakieta zakoczya obrt
    }

    // Normalizuj wysoko do zakresu od 0 do 1
    let normalizedHeight = (height - turnStartHeight) / (turnEndHeight - turnStartHeight);

    // Oblicz kt za pomoc funkcji logarytmicznej
    let angle = maxAngle * Math.log10(1 + 7.5 * normalizedHeight); // 9 to wspczynnik dopasowujcy krzyw


    // Upewnij si, e kt nie przekracza maksymalnego kta
    return Math.min(angle, maxAngle);
}

// Przykad uycia:
//let height = 50000; // Wysoko rakiety w metrach
//let angle = gravityTurnAngle(height);
// console.log(`Kt lotu rakiety na wysokoci ${height} metrw to ${angle.toFixed(2)} stopni.`);

export default gravityTurnAngle